@mixin tablet {
  @media (min-width: (640px)) {
    @content;
  }
}

@mixin tablet-large {
  @media (min-width: (1024px)) {
    @content;
  }
}

@mixin desktop {
  @media (min-width: (1240px)) {
    @content;
  }
}

@mixin mobile {
  @media (max-width: (1024px)) {
    @content;
  }
}
